import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

class AlterarMaquinaTipo extends Component {
    state = {
        id: '',
        nome: '',
        tipo: '',
        tipoMaquina: [],
        marca: '',
        modelo: '',
        localizacao: '',
        maquinasAlterar: [],
        listaMaquinas: [],
        selected_tipo:[],
        data: false
    }

    changeSelect = (e) => {
        const data = this.state.listaMaquinas.filter(el => el.id === e.target.value);
        this.setState({ maquina: data[0]});
        this.setState({ nome: data[0].nome});
        this.setState({ modelo: data[0].modelo});
        this.setState({ marca: data[0].marca});
        this.setState({ localizacao: data[0].localizacao});
        this.setState({ tipo: data[0].tipo});

    };

    changeSelect2 = (e) => {
        const data = this.state.tipoMaquina.filter(el => el.id === e.target.value);
        this.setState({ tipo: data[0]});
    };

    handleChange = (e) => {
        const value = e.target.value;
        this.setState({
            ...this.state, [e.target.name]:value
        });
    };

    handleClick = (e) => {
        e.preventDefault();
        axios.put('https://mdf37.azurewebsites.net/api/maquina/'+ this.state.maquina.id + '/' + this.state.tipo.id,
            {
                "nome" : this.state.nome,
                "modelo" : this.state.maquina.modelo,
                "localizacao" : this.state.maquina.localizacao,
                "tipo" : this.state.tipo
            }).then((response) => {
            window.alert("Maquina alterada com sucesso!")
            console.log(response);
        }, (error) => {
            console.log(error);
            window.alert("Sem sucesso!")
        })
    }

    async componentWillMount() {
        await axios.get('https://mdf37.azurewebsites.net/api/GetAllMaquinas').then(response => this.setState({ listaMaquinas: response.data}))
        this.setState({ data: true });
        await axios.get('https://mdf37.azurewebsites.net/api/TipoMaquina/GetAllTipoMaquinas').then(response => this.setState({ tipoMaquina: response.data }))

    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (

            <div className="card z-depth-0 project-summary">
                <h5 className="grey-text text-darken-3 center">Alterar máquina</h5>
                <div>
                    <select className="browser-default" onChange={this.changeSelect}>
                        {this.state.listaMaquinas.map(
                            (m) => <option key={m.id} value={m.id}>{m.nome} </option>)}
                    </select>
                    <div >
                        <label> <h6><b>Nome:</b> {this.state.nome} </h6></label>
                        <br/>
                        <label> <h6><b>Marca:</b> {this.state.marca} </h6></label>
                        <br/>
                        <label> <h6><b>Modelo:</b> {this.state.modelo} </h6></label>
                        <br/>
                        <label> <h6><b>Localização:</b> {this.state.localizacao} </h6></label>
                        <br/>

                    </div>
                <br/>
                    <h6 className="grey-text text-darken-3" > Selecione um novo tipo de máquina</h6>
                    <select className="browser-default" onChange={this.changeSelect2}>
                        {this.state.tipoMaquina.map(
                            (tm) => <option key={tm.id} value={tm.id}>{tm.tipo} </option>)}

                    </select>
                </div>
                <div className="input-group">
                    <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Gravar Alterações</button>
                    </span>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(AlterarMaquinaTipo)