import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'


class ConsultarPlanoDeProduto extends Component {
    state = {
        id: '',
        nome: '',
        produtos: [],
        planoproduto: [],
        operacoes: [],
        data: false
    }

    async componentWillMount() {
            await axios.get('https://mdp37.azurewebsites.net/api/GetAllProdutos').then(response => {
            this.setState({ produtos: response.data })
            const data = response.data
            console.log(data[0])
            this.setState({ planoproduto: data[0].pFabrico })
            this.setState({ nome: data[0].pFabrico.nome })
            this.setState({ operacoes: data[0].pFabrico.planoFabricoOperacao })
            this.setState({ data: true })
        })
    }

    changeSelect = (e) => {
        const data = this.state.produtos.filter(el => el.id === e.target.value);
        this.setState({ id: data[0].id });
        this.setState({ nome: data[0].pFabrico.nome });
        this.setState({ operacoes: data[0].pFabrico.planoFabricoOperacao })
    }

    handleClick = () => {
        this.props.history.push("/planofabricomenu");
    }

    render() {
        if (!this.state.data) {
            return <div />
        }
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="card z-depth-0 project-summary">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3 center">Consultar Plano de Fabrico de um Produto</h5>
                    <div>
                        <select className="browser-default" onChange={this.changeSelect}>
                            {this.state.produtos.map((p) => <option key={p.id} value={p.id}>{p.nome}</option>)}
                        </select>

                    </div>
                    <div className="mostrarplanodoproduto-group" >
                        <label> <h6>

                            <b>Nome do plano de fabrico: </b> {this.state.nome}<br />
                            <div>
                                {this.state.operacoes.map((op) => (<item> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; - {op.nome} <br /><br /></item>))}
                            </div>
                        </h6></label>
                    </div>
                    <div className="input-group center">
                        <span className="input-group-btn">
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Voltar</button> &nbsp;&nbsp;&nbsp;
                    </span>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(ConsultarPlanoDeProduto)