import React, { Component } from 'react'
import { connect } from 'react-redux'
import TipoMaquinasSumario from './TipoMaquinasSumario'
import axios from 'axios'
import { Redirect } from 'react-router-dom'


class ListarTipoMaquina extends Component {
    state = {
        //id: '',
        tipo: '',
        // tipo: '',
         tipomaquina: [],
        // marca: '',
        // modelo: '',
        // localizacao: '',
        tipomaquinas:[]
    }

    handleClick = () => {
        this.props.history.push("/tipomaquinamenu");
    }
    
    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="card z-depth-0 project-summary">
                <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Voltar</button>
                </span>
                <h5 className="grey-text text-darken-3 center">##Tipos de Maquina##</h5>
                <div className="mostrarmaquinas-group">
                    {this.state.tipomaquinas.map((tipo) => (
                        <TipoMaquinasSumario key={tipo.tipo} tipomaquina={tipo} />)
                    )}
                </div>
            </div>
        )
    }

    async componentWillMount() {
        await axios.get('https://mdf37.azurewebsites.net/api/TipoMaquina/GetAllTipoMaquinas').then(response => this.setState({ tipomaquinas: response.data }))
     }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(ListarTipoMaquina)