import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

class Listar extends Component {
   
    

    handleClick = () => {
        this.props.history.push("/criarmaquina");
    }

    handleClick1 = () => {
        this.props.history.push("/maquina")
    }

    handleClick2 = () => {
        this.props.history.push("/listarmaquinatipo")
    }

    handleClick3 = () => {
        this.props.history.push("/alterarmaquinatipo")
    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="card z-depth-0 project-summary">
                <span className="input-group-btn">
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Listar Máquinas</button> &nbsp;&nbsp;&nbsp;
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}>Listar Máquina por tipo</button> &nbsp;&nbsp;&nbsp;
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Criar Máquinas</button> &nbsp;&nbsp;&nbsp;
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick3}>Alterar Tipo de Máquinas</button>
                </span>
            </div>
        )
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(Listar)
