import React from 'react'

const TipoMaquinasSumario = ({tipomaquina}) => {
  console.log("aaaaaaaaaaaaaaaaa ", tipomaquina)
  return (
    <div className="card z-depth-0 project-summary ">
      <div className="card-content grey-text text-darken-3 ">
        <span className="font-size:50%card-title ">Tipo: {tipomaquina.tipo}</span> 
        <span className="font-size:50%card-title "> <br/>Operações: {tipomaquina.tipoMaquinaOperacao.map((op) =>(<item> <p> - {op.operacao.nome} </p></item>))}</span>
      </div>
    </div>
  )
}

export default TipoMaquinasSumario