import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

class EncomendaMenu extends Component {


    handleClick1 = () => {
        this.props.history.push("/criarencomendas");
    };

    handleClick2 = () => {
        this.props.history.push("/consultarencomendas");
    };

    handleClick3 = () => {
        this.props.history.push("/cancelarencomendas");
    };

    handleClick4 = () => {
        this.props.history.push("/alterarencomendas");
    };

    render() {
        const {auth} = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin'/>
        if (localStorage.getItem('isAdmin') === "false") {
            return (
                <div className="card z-depth-0 project-summary white">
                    <h5 className="grey-text text-darken-3 center">Encomendas</h5>
                    <br/>
                    <div className="input-group center">
                        <span className="input-group-btn">
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}>Consultar Encomendas</button>
                            &nbsp;&nbsp;&nbsp;
                            <button className="btn pink lighten-1 z-depth-0"
                                    onClick={this.handleClick1}>Criar Encomenda</button>
                            &nbsp;&nbsp;&nbsp;
                        </span>
                    </div>
                </div>
            )
        }else{
            return (
                <div className="card z-depth-0 project-summary white">
                    <h5 className="grey-text text-darken-3 center">Encomendas</h5>
                    <br/>
                    <div className="input-group center">
                        <span className="input-group-btn">
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}>Consultar Encomendas</button>
                            &nbsp;&nbsp;&nbsp;
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick4}>Alterar Encomenda</button>
                            &nbsp;&nbsp;&nbsp;
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick3}>Cancelar Encomenda</button>
                        </span>
                    </div>
                </div>
            )
        }
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(EncomendaMenu)