import React from 'react'
import {NavLink} from 'react-router-dom'
import {connect} from 'react-redux'
import {signOut} from '../store/actions/login/authActions'

const links = (props) => {
    return (
        <ul className="right">
            <li><NavLink to='/operacoes'>Operacoes</NavLink></li>
            <li><NavLink to='/tipomaquinamenu'>Tipo de Maquina</NavLink></li>
            <li><NavLink to='/linhaproducao'>Linha de Producao</NavLink></li>
            <li><NavLink to='/maquinamenu'>Maquina</NavLink></li>
            <li><NavLink to='/produto'>Produto</NavLink></li>
            <li><NavLink to='/planofabricomenu'>Plano de Fabrico</NavLink></li>
            <li><NavLink to='/areacliente'>Área do Cliente</NavLink></li>
            <li><NavLink to='/encomendas'>Encomendas</NavLink></li>
            <li><NavLink to='/sgrai'>Fabrica Sgrai</NavLink></li>
            <li><a onClick={props.signOut}>Logout</a></li>
            <li><NavLink to='/'className='btn btn-floating pink lighten-1'>ra1n</NavLink></li>
        </ul>
    )
}

const mapDispatchToProps = (dispatch) => {
    return {
        signOut: () => dispatch(signOut())
    }
}

export default connect(null, mapDispatchToProps)(links)