import React from 'react'

const MaquinasSumario = ({maquina}) => {
  return (
    <div className="card z-depth-0 project-summary">
      <div className="card-content grey-text text-darken-3">
        <span className="front-size:50%card-title ">Nome: {maquina.nome}</span>
        <span className="front-size:50%card-title "> <br/>Marca: {maquina.marca}</span>
        <span className="front-size:50%card-title "> <br/>Modelo: {maquina.modelo}</span>
        <span className="front-size:50%card-title "> <br/>Tipo: {maquina.tipo.tipo}</span>
        <span className="front-size:50%card-title "> <br/>Localização: {maquina.localizacao}</span>
      </div>
    </div>
  )
}

export default MaquinasSumario