import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

class AreaCliente extends Component {

    handleClick = () => {
        this.props.history.push("/alterarcliente");
    }

    handleClick1 = () => {
        this.props.history.push("/consultarcliente");
    }

    handleClick2 = () => {
        this.props.history.push("/");
    }

    render() {
        const { auth } = this.props;
        //if (!auth.isLoaded) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="card z-depth-0 project-summary">
                <span className="input-group-btn">
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Alterar Dados</button> &nbsp;&nbsp;&nbsp;
                    <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Consultar Dados</button>
                    <div className="right" >
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}>Voltar</button>
                    </div>
                </span>
            </div>
        )
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(AreaCliente)