import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

class TipoMaquinaMenu extends Component {


    handleClick = () => {
        this.props.history.push("/criartipomaquina");
    }

    handleClick2 = () => {
        this.props.history.push("/listartipomaquina");
    }

    handleClick3 = () => {
        this.props.history.push("/alterartipomaquinaoperacao");
    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="card z-depth-0 project-summary white">
                <h5 className="grey-text text-darken-3 center">Tipo de Maquina</h5>
                <br />
                <div className="input-group center">
                    <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}> Listar Tipo de Maquinas </button> &nbsp;&nbsp;&nbsp;
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Criar Tipo de Maquina</button> &nbsp;&nbsp;&nbsp;
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick3}>Alterar Tipo de Maquina</button>
                    </span>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    //state.operacao.operacoes.map((nome) => { console.log(nome.nome, nome.id)})
    /*for( var x in state.operacao.operacoes){
      console.log(x, state.operacao.operacoes[x])
    }*/
    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(TipoMaquinaMenu)