import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { criarMaquina } from '../../store/actions/maquina/MaquinaActions'
import axios from 'axios'


class CriarMaquina extends Component {
    state = {
        id: '',
        nome: '',
        tipo: '',
        tipoMaquina: [],
        marca: '',
        modelo: '',
        localizacao: ''
    }   

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    changeSelect = (e) => {
        console.log(this.state.tipo)
        this.setState({
            tipo: e.value
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        console.log("AVISO", this.state)
        axios.post('https://mdf37.azurewebsites.net/api/maquina',
            {
                "nome": this.state.nome,
                "marca": this.state.marca,
                "modelo": this.state.modelo,
                "localizacao": this.state.localizacao,
                "tipo": this.state.tipo
            }).then((response) => {

                // this.props.criarMaquina({
                //     id : response.data.id,
                //     nome : response.data.nome,
                //     marca: marca.data.marca,
                //     modelo : modelo.data.modelo,
                //     localizacao : localizacao.data.localizacao,
                // })

                window.alert("Máquina criada com sucesso!")
                this.setState({
                    nome: '',
                    tipo: '',
                    marca: '',
                    modelo: '',
                    localizacao: ''
                })

                console.log(response);
            }, (error) => {
                console.log(error);
                window.alert("Sem sucesso!")
            })
    }

    async componentWillMount() {
        axios.get('https://mdf37.azurewebsites.net/api/TipoMaquina/GetAllTipoMaquinas').then(response => this.setState({ tipoMaquina: response.data }))
    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="container">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3">Criar nova maquina</h5>
                    <div className="input-field">
                        <textarea id="nome" value={this.state.nome} placeholder="Nome da maquina" className="materialize-textarea" onChange={this.handleChange}></textarea>
                        <textarea id="marca" value={this.state.marca} placeholder="Marca da maquina" className="materialize-textarea" onChange={this.handleChange}></textarea>
                        <textarea id="modelo" value={this.state.modelo} placeholder="Modelo da maquina" className="materialize-textarea" onChange={this.handleChange}></textarea>
                        <textarea id="localizacao" value={this.state.localizacao} placeholder="Localização da maquina" className="materialize-textarea" onChange={this.handleChange}></textarea>
                    </div>
                    <div>
                        <select className="browser-default" value={this.state.tipo}
                            onChange={(e) => this.setState({ tipo: e.target.value }, console.log(this.state.tipo))}>
                            {this.state.tipoMaquina.map((tm) => <option key={tm.id} value={tm.id}>{tm.tipo}</option>)}
                        </select>
                    </div>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-0">Criar</button>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    console.log("pipipi", state)
    return {
        auth: state.auth
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        criarMaquina: (maquina) => dispatch(criarMaquina(maquina))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CriarMaquina)



