import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'

class AlterarCliente extends Component {
    state = {
        id: '',
        nome: '',
        morada: '',
        clientes: [],
    }

    changeSelect = (e) => {
        const data = this.state.clientes.filter(el => el.nome === e.target.value);
        this.setState({ id: data[0]._id });
        this.setState({ nome: data[0].nome });
        this.setState({ morada: data[0].morada });
    };

    handleChange = (e) => {
        const value = e.target.value;
        this.setState({
            ...this.state, [e.target.id]: value
        });
    };

    handleClick1 = () => {
        this.props.history.push("/areacliente");
    }

    handleClick = (e) => {
        e.preventDefault();
        console.log("AVISO", this.state);
        axios.put('http://10.9.10.37:3001/clientes/' + localStorage.getItem('cli_id'),
            {
                "nome": this.state.nome,
                "morada": this.state.morada,
            }).then((response) => {
                window.alert("Dados do cliente alterados com sucesso!")
                console.log(response);
                this.props.history.push("/areacliente");
            }, (error) => {
                console.log(error);
                window.alert("Sem sucesso!")
            })
           
    }

    async componentWillMount() {
        await axios.get('http://10.9.10.37:3001/clientes/' + localStorage.getItem('cli_id')).then(response => this.setState({ clientes: response.data }))
        this.setState({ data: true });
    }

    render() {
        const { auth } = this.props;
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (

            <div className="card z-depth-0 project-summary">

                <h5 className="grey-text text-darken-3 center">Alterar máquina</h5>
                <div>
                    <label> <h6><b>Nome:</b>{this.state.clientes.map((cl) => <option key={cl.id} value={cl.id}>{cl.nome} </option>)} </h6></label>
                    <br />
                    <label> <h6><b>Morada:</b>{this.state.clientes.map((cl) => <option key={cl.id} value={cl.id}>{cl.morada} </option>)} </h6></label>
                    <br />
                    <h6 className="grey-text text-darken-3" > Indique os novos dados do cliente </h6>
                    <div className="browser-default">
                        <textarea id="nome" value={this.state.nome} placeholder="Nome" className="materialize-textarea" onChange={this.handleChange}></textarea>
                        <textarea id="morada" value={this.state.morada} placeholder="Morada" className="materialize-textarea" onChange={this.handleChange}></textarea>
                    </div>
                </div>
                <div className="input-group">
                    <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Gravar Alterações</button>
                    </span>
                    <div className="right" >
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Voltar</button>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(AlterarCliente)