import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'


class ConsultarCliente extends Component {
    state = {
        nome: '',
        email: '',
        morada: '',
        nif: '',
        clientes: []
    }

    changeSelect = (e) => {
        const data = this.state.clientes.filter(el => el.nome === e.target.value);
        this.setState({ nome: data[0].nome });
        this.setState({ email: data[0].email });
        this.setState({ morada: data[0].morada });
        this.setState({ nif: data[0].nif });
        //this.setState({ id: data[0].id });
    }

    handleClick = (e) => {
        console.log(this.state)
        e.preventDefault();
        axios.get('http://10.9.10.37:3001/clientes/' + localStorage.getItem('cli_id')).then(response => this.setState({ clientes: response.data }))
    }

    handleClick1 = () => {
        this.props.history.push("/areacliente");
    }

    render() {
        const { auth } = this.props;
        //if (!auth.isLoaded) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        if(localStorage.getItem('isAdmin') === "false") {
            return (

                <div className="card z-depth-0 project-summary">
                    <form onSubmit={this.handleSubmit} className="white">
                        <h5 className="grey-text text-darken-3 center">Dados do Cliente</h5>
                        <div>
                            <label><h6><b>Nome:</b>{this.state.clientes.map((cb) => <option key={cb.id}
                                                                                            value={cb.id}>{cb.nome} </option>)}
                            </h6></label>
                            <br/>
                            <label><h6><b>E-mail:</b>{this.state.clientes.map((cl) => <option key={cl.id}
                                                                                              value={cl.id}>{cl.email} </option>)}
                            </h6></label>
                            <br/>
                            <label><h6><b>Morada:</b>{this.state.clientes.map((cl) => <option key={cl.id}
                                                                                              value={cl.id}>{cl.morada} </option>)}
                            </h6></label>
                            <br/>
                            <label><h6><b>NIF:</b>{this.state.clientes.map((cl) => <option key={cl.id}
                                                                                           value={cl.id}>{cl.nif} </option>)}
                            </h6></label>
                            <br/>
                        </div>
                    </form>
                    <div className="right">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Voltar</button>
                    </div>
                </div>
            )
        }else{
            return (

                <div className="card z-depth-0 project-summary">
                    <form onSubmit={this.handleSubmit} className="white">
                        <h5 className="grey-text text-darken-3 center">Dados do Cliente</h5>
                        <div>
                            <label><h6><b>Nome:</b>{this.state.clientes.map((cb) => <option key={cb.id}
                                                                                            value={cb.id}>{cb.nome} </option>)}
                            </h6></label>
                        </div>
                    </form>
                    <div className="right">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Voltar</button>
                    </div>
                </div>
            )
        }
    }

    async componentWillMount() {
        axios.get('http://10.9.10.37:3001/clientes/' + localStorage.getItem('cli_id')).then(response => this.setState({ clientes: response.data }))
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(ConsultarCliente)