import React, { Component } from 'react'
import { criarOperacao } from '../../store/actions/operacao/OperacaoActions'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'
import axios from 'axios'

class CriarOperacao extends Component {
    state = {
        id: '',
        nome: ''
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    handleSubmit = (e) => {
        e.preventDefault();
        axios.post('https://mdf37.azurewebsites.net/api/operacao',
            {
                "nome": this.state.nome
            }).then((response) => {
                this.props.criarOperacao({
                    id : response.data.id,
                    nome : response.data.nome
                })
                window.alert("Com sucesso!")
                this.setState({
                    nome: ''
                })
                console.log(response);
            }, (error) => {
                console.log(error);
                window.alert("Sem sucesso!")
            })
    }
    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (
            <div className="container">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3">Criar nova operacao</h5>
                    <div className="input-field">
                        <textarea id="nome" value={this.state.nome} placeholder="Nome da operacao" className="materialize-textarea" onChange={this.handleChange}></textarea>
                    </div>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-0">Criar</button>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    console.log("pipipi", state)
    return {
        auth: state.auth
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        criarOperacao: (operacao) => dispatch(criarOperacao(operacao))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CriarOperacao)
