import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Redirect } from 'react-router-dom'


class ListarPlanoFabrico extends Component {
    state = {
        planosfabrico: []
    }

    handleClick1 = () => {
        this.props.history.push("/criarplanoFabrico");
    }

    handleClick2 = () => {
        this.props.history.push("/planofabricomenu");
    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (

            <div className="container">
                <form onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3 center">Planos de Fabrico</h5>
            <div className="card z-depth-0 project-summary">
                <div>
                    {this.state.planosfabrico.map((pf) =>(<item><b>Nome: </b>{pf.nome} <br/>
                        {pf.planoFabricoOperacao.map((op) =>(<item> &nbsp;&nbsp;&nbsp;&nbsp;<b>-</b> {op.operacao.nome} <br/></item>))}<item></item><br/></item> ))}
                </div>
                <br/>
                <div className="input-group center">
                    <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick2}>Voltar</button> &nbsp;&nbsp;&nbsp;
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick1}>Criar Plano de Fabrico</button>
                    </span>
                </div>
                 </div>
                </form>
            </div>
        )
    }

    async componentWillMount() {
        axios.get('https://mdp37.azurewebsites.net/api/GetAllPlanosFabrico').then(response => this.setState({ planosfabrico: response.data}))
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(ListarPlanoFabrico)