import React, { Component } from 'react'
import { connect } from 'react-redux'
import MaquinasSumario from './MaquinasSumario'
import axios from 'axios'
import { Redirect } from 'react-router-dom'


class ListarMaquinas extends Component {
    state = {
        nome: '',
        maquinas: []
    }

    handleClick = () => {
        this.props.history.push("/criarmaquina");
    }

    render() {
        const { auth } = this.props;
        //if \(!auth\.isLoaded\) return null
        if (localStorage.getItem('logged') != 'true') return <Redirect to='/signin' />
        return (

            <div className="card z-depth-0 project-summary">
                <h5 className="grey-text text-darken-3 center">Lista de Máquinas</h5>
                <div className="mostrarmaquinas-group">
                    {this.state.maquinas.map((nome) => (
                        <MaquinasSumario key={nome.nome} maquina={nome} />)
                    )}
                </div>
                <div className="input-group">
                    <span className="input-group-btn">
                        <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Criar Maquina</button>
                    </span>
                </div>
            </div>
        )
    }

    async componentWillMount() {
        await axios.get('https://mdf37.azurewebsites.net/api/GetAllMaquinas').then(response => this.setState({ maquinas: response.data }))
    }
}

const mapStateToProps = (state) => {

    return {
        auth: state.auth
    }
}

export default connect(mapStateToProps)(ListarMaquinas)