import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { criarCliente } from '../store/actions/cliente/ClienteActions'
import axios from 'axios'

class SignUp extends Component {
    state = {
        nome: '',
        morada: '',
        nif: '',
        email: '',
        password: '',
        isAdmin: false
        //confirmPassword: ''
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    handleClick = () => {
        this.props.history.push("/signin");
    }

    handleClick1 = () => {
        this.props.history.push("/termos");
    }

    validateNIF = () => {
        if (this.state.nif.length == 9) {
            var added = ((this.state.nif[7] * 2) + (this.state.nif[6] * 3) + (this.state.nif[5] * 4) + (this.state.nif[4] * 5) + (this.state.nif[3] * 6) + (this.state.nif[2] * 7) + (this.state.nif[1] * 8) + (this.state.nif[0] * 9));
            var mod = added % 11;
            var control;
            if (mod == 0 || mod == 1) {
                control = 0;
            } else {
                control = 11 - mod;
            }

            if (this.state.nif[8] == control) {
                return true;
            } else {
                alert("NIF inválido. Tente novamente, por favor.");
                return false;
            }
        } else {
            alert("NIF inválido. Tente novamente, por favor.");
            return false;
        }
    }

    // validatePassword = () => {
    //     this.state.password = document.getElementById("password").value;
    //     this.state.confirmPassword = document.getElementById("confirmPassword").value;
    //     if (this.state.password != this.state.confirmPassword) {
    //         alert("Passwords não coincidem. Tente novamente, por favor.");
    //         return false;
    //     }
    //     return true;
    // }


    handleSubmit = (e) => {
        e.preventDefault();
        if (this.validateNIF() == true) {
            axios.post('http://10.9.10.37:3001/clientes/signup',
                {
                    "nome": this.state.nome,
                    "morada": this.state.morada,
                    "nif": this.state.nif,
                    "email": this.state.email,
                    "password": this.state.password,
                    "isAdmin": false
                    //"confirmPassword": this.state.confirmPassword
                }).then((response) => {
                    window.alert("Cliente registado com sucesso!")
                    document.getElementById("abanador").reset();
                    this.setState({
                        nome: '',
                        morada: '',
                        nif: '',
                        email: '',
                        password: ''
                        //confirmPassword: ''
                    })

                    console.log(response);
                }, (error) => {
                    console.log(error);
                    window.alert("Sem sucesso!")
                })
        } 
    }

    render() {
        const { auth } = this.props;
        if (localStorage.getItem('logged') == 'true') return <Redirect to='/' />
        return (
            <div className="container">
                <form id="abanador" onSubmit={this.handleSubmit} className="white">
                    <h5 className="grey-text text-darken-3">SignUp</h5>
                    <p>Preencha os seguintes campos para efetuar o seu registo.</p>
                    <div className="input-field">
                        <label htmlFor="nome">Nome</label>
                        <input type="text" id="nome" required="true" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <label htmlFor="morada">Morada</label>
                        <input type="text" id="morada" required="true" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <label htmlFor="nif">NIF</label>
                        <input type="number" id="nif" required="true" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <label htmlFor="email">E-mail</label>
                        <input type="email" id="email" required="true" onChange={this.handleChange} />
                    </div>
                    <div className="input-field">
                        <label htmlFor="password">Password</label>
                        <input type="password" id="password" required="true" onChange={this.handleChange} />
                    </div>
                    <p>Ao criar uma nova conta, aceita os nossos <a href="#" onClick={this.handleClick1}>Termos da politica de privacidade</a>.</p>
                    <div className="input-field">
                        <button className="btn pink lighten-1 z-depth-0">Confirmar Registo</button>
                        <div className="right" >
                            <button className="btn pink lighten-1 z-depth-0" onClick={this.handleClick}>Login</button>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    console.log("pipipi", state)
    return {
        auth: state.auth
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        criarCliente: (cliente) => dispatch(criarCliente(cliente))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp)
